* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #f5f5f5;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

.game-container {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  width: 350px;
  padding: 40px;
  box-shadow: 0 2px 6px #0000001a, 0 1px 3px #00000014;
}

h1 {
  color: #202124;
  margin-bottom: 20px;
  font-size: 2rem;
  font-weight: 500;
}

.result-area {
  color: #202124;
  margin-bottom: 15px;
  font-size: 1.2rem;
  font-weight: 400;
}

.chance-area {
  color: #5f6368;
  margin-bottom: 25px;
  font-size: 1.1rem;
}

.input-field {
  border: 1px solid #dcdfe1;
  border-radius: 4px;
  outline: none;
  width: 100%;
  max-width: 270px;
  margin-bottom: 20px;
  padding: 12px;
  font-size: 1rem;
  transition: border-color .2s;
}

.input-field:focus {
  border-color: #4caf50;
  box-shadow: 0 0 0 2px #4caf5033;
}

.buttons {
  justify-content: center;
  gap: 15px;
  display: flex;
}

.buttons button {
  color: #fff;
  cursor: pointer;
  background-color: #4285f4;
  border: none;
  border-radius: 4px;
  width: 120px;
  padding: 12px 25px;
  font-size: 1rem;
  transition: background-color .2s, transform .1s;
}

.buttons button:hover {
  background-color: #357ae8;
  transform: translateY(-2px);
}

.buttons button:disabled {
  cursor: not-allowed;
  background-color: #b0bec5;
}

.buttons button:disabled:hover {
  background-color: #b0bec5;
  transform: none;
}
